import React from "react";
// @material-ui/core components
import {makeStyles} from "@material-ui/core/styles";
import styles from "assets/jss/material-kit-react/components/offerActions.js";
import Button from "../CustomButtons/Button";
import classNames from "classnames";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles(styles);


export default function OfferActions(props) {
    const classes = useStyles();
    const {product} = props;

    return (
        <div className={classNames(classes.main)}>
            <Typography variant="subtitle1" color="textPrimary" component="p">
                 : <br/>
                <span style={{fontWeight: "bold", color: "#000", textAlign: "center"}}>
                    {product.price ? product.price : " "}
                </span>
            </Typography>
            <Typography variant="subtitle1" color="textPrimary" component="p">
                 : <br/> <span
                style={{fontWeight: "bold", color: "#000", textAlign: "center"}}>
                {product.deadline ? new Date(product.deadline).toDateString() : " "}
            </span>
            </Typography>

            <Button color="success" className={classNames(classes.btn)}></Button>
            <Button color="danger" className={classNames(classes.btn)}></Button>
        </div>
    );
}

